import React from "react"
import { Page, StatsCard, Grid, Alert, Header, Dimmer } from "tabler-react"
import { Helmet } from "react-helmet";
import axios from "axios"

import Layout from "../components/layout"
import SEO from "../components/seo"


const MHeader2 = ({ label }) => (
  <Grid.Col width={1}>
    <Header.H4>{ label }</Header.H4>
  </Grid.Col>
)

const MStatsCard = ({ label, total, movement }) => (
  <Grid.Col lg={2} md={4} sm={6} xs={12}>
    <StatsCard layout={1} movement={ movement } total={total} label={label} />
  </Grid.Col>
)

const MLoader = () => (
  <Grid.Col width={12}>
    <Dimmer loader active>
      載入中...
    </Dimmer>
  </Grid.Col>
)

const DashboardRow = ({ row }) => {
  var items;
  if (row && row.length > 0)
    items = row.map(({ type, ...props }, index) => {
      console.log( type );
      if (type==="Loader")
        return (<MLoader key={index}/>)
      if (type==="StatsCard")
        return (<MStatsCard key={index} {...props} />)
      if (type==="Header.H2")
        return (<MHeader2 key={index} {...props} />)
      return (<></>)
    })
  if (items)
    return (<>{items}</>);
  else
    return (<></>);
}

const Dashboard = ({ board }) => {
  var items;
  if (board && board.length > 0)
    items = board.map(( row , index) => {
      console.log(row);
      return (<Grid.Row key={index}><DashboardRow row={ row }/></Grid.Row>)
    })
  if (items)
    return (<>{items}</>);
  else
    return (<></>);
}


class IndexPage extends React.Component {
  
  fetchOnline(){    
    var _this = this;
    
      axios
        .get(`https://ourhealthcarehk.github.io/api/dashboard.json`)
        .then(({ data }) => {
          
          
          if (!data)
            return;
          
          _this.setState({ dashboard: data });
                    
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
  }
  
  constructor() {
    super()
    this.state = { dashboard:[[{ type: "Loader" }]] };
  }  
  
	componentDidMount() {
    this.fetchOnline();
	}
  
  render() {
    return (
  <Layout>
    <SEO title="主頁" />
    <Helmet
        meta={[
        { name: 'google-site-verification', content: 'Jny2bg5DxLZp7_8rP3CCkemLdYCSaA4Ruv0F_6GVQcA' }
        ]}
    />
    <Page.Content title="主頁">
    <Alert type="secondary">你可以在上面的工具欄選取你想瀏覽的資料。</Alert>
    <Alert type="primary">你好！我們正在建設這個網站，部分資料可能有延遲或錯漏。</Alert>
    <div>
      <Dashboard board={this.state.dashboard} />
    </div>
    </Page.Content>
  </Layout>
);
    
  }
}

export default IndexPage
